import axiosInstance from "@/api/axios/axiosInstance";

export const getAllClinicalSites = async () => {
  try {
    const response = await axiosInstance.get("/api/clinical-sites");
    return response.data;
  } catch (error) {
    console.error("Error fetching clinical sites:", error);
    throw error;
  }
};

export const getClinicalSiteById = async (id: string) => {
  try {
    const response = await axiosInstance.get(`/api/clinical-sites/${id}`);
    return response.data;
  } catch (error) {
    console.error("Error fetching clinical site:", error);
    throw error;
  }
};

export const createClinicalSite = async (siteData: any) => {
  try {
    const response = await axiosInstance.post("/api/clinical-sites", siteData);
    return response.data;
  } catch (error) {
    console.error("Error creating clinical site:", error);
    throw error;
  }
};

export const updateClinicalSite = async (id: string, siteData: any) => {
  try {
    const response = await axiosInstance.put(`/api/clinical-sites/${id}`, siteData);
    return response.data;
  } catch (error) {
    console.error("Error updating clinical site:", error);
    throw error;
  }
};

export const deleteClinicalSite = async (id: string) => {
  try {
    const response = await axiosInstance.delete(`/api/clinical-sites/${id}`);
    return response.data;
  } catch (error) {
    console.error("Error deleting clinical site:", error);
    throw error;
  }
};

